<template>
  <div id="app">
    <!-- <h1>crosstalk</h1> -->
    
    <button @click.prevent="newRound()">
      new round
    </button>

    <button @click.prevent="showView = !showView">
      toggle screen
    </button>

    <div id="desc">
      <!-- click anywhere or press space to generate new words -->
    </div>

    <div class="container">
      <div @mousedown.prevent="drag=true;pointDial($event)"
          @mousemove.prevent="pointDial($event)"  
          @mouseup.prevent="drag=false"
          id="spectrum">
        <div :style="specInnerStyle" class="spec-inner" >
          <div class="spec-slice base l"></div>
          <div class="spec-slice big l"></div>
          <div class="spec-slice med l"></div>
          <div class="spec-slice small"></div>
          <div class="spec-slice med r"></div>
          <div class="spec-slice big r"></div>
          <div class="spec-slice base r"></div>
        </div>
        <transition name="fade">
          <div v-if="showView" class="spec-view">
          </div>
        </transition>
        <div class="spec-border"></div>      
        <div class="spec-extra l"></div>
        <div class="spec-extra r"></div>
        
        <div :style="dialStyle" id="dial">
          <div ref="knob" id="dial-knob"></div>
          <div id="dial-needle"></div>
        </div>
      </div>
    </div>


    <div id="cards">
      <div class="card" v-for="card in cards" :key="card.left+card.right">
        <div class="card-left"  :style="{'background-color': genColor()}">{{card.left}}</div>
        <div class="card-right" :style="{'background-color': genColor()}">{{card.right}}</div>
      </div>
    </div>

  </div>
</template>

<script>
// using raw-loader in webpack: see vue.config.js
import wordlist from './assets/words_codenames.txt'
import cards from './assets/cards.json'

export default {
  name: 'app',
  data() {
    return {
      cards: [],
      wordlist: [],
      specInnerStyle: {
        transform: ""
      },
      dialStyle: {
        transform: ""
      },
      showView: false,
      drag: false,
    }
  },

  created() {
    // get wordlist
    this.wordlist = wordlist.trim().split('\n')
    // window.console.log(cards)
  },
  mounted() {
    window.addEventListener('keydown', (ev)=> {
      if (ev.key == " ") {
        this.showView = !this.showView
      } else if (ev.key == "r") {
        this.newRound()
      }
    })

    this.newRound()
  },

  
  
  methods: {
    randColorStyle: function() {
      return {
        'background-color': this.genColor()
      }
    },

    randRange: function(min, max) {
      return Math.random() * (max - min) + min;
    },

    // given polar angle, return rotate string
    polarRotateString: function(angle) {
      if (angle < 0)
        return 'rotate(' + (-angle) + 'deg)'
      else
        return 'rotate(-' + angle + 'deg)'

    },

    genColor: function() {
      var h = this.randRange(1, 360)
      var s = this.randRange(0, 100)
      var l = this.randRange(60, 100)
      return 'hsl(' + h + ',' + s + '%,' + l + '%)'
    },

    newRound: function() {
      // disallow new round when view is hidden
      if (this.showView) return

      this.newAngle()
      this.genCards()

      // hide the dial
      this.dialStyle['transform'] = this.polarRotateString(200)
    },

    newAngle: function() {

      // generate new angle for the wavelength spectrum
      const minAngle = 13.5 // min angle for blue to show
      const maxAngle = 180 - minAngle;
      const angle = this.randRange(minAngle, maxAngle);


      // this.specInnerStyle['transform'] = 'rotate(-' + minAngle + 'deg)'
      this.specInnerStyle['transform'] = this.polarRotateString(angle)
      window.console.log("new angle")
    },

    pointDial: function(ev) {

      if (!this.drag) return

      // window.console.log("pointing dial")
      // window.console.log(this.$refs.knob.getBoundingClientRect())

      const knob = this.$refs.knob.getBoundingClientRect()

      let dialX = knob.x + knob.width/2
      let dialY = knob.y + knob.height/2

      let dx = ev.x - dialX
      let dy = ev.y - dialY

      const angle = 180/Math.PI * Math.atan2(-dy, dx)

      // window.console.log(angle)

      this.dialStyle['transform'] = this.polarRotateString(angle-90)
      // this.dialStyle['transform'] = this.polarRotateString(20)

      // window.console.log(this.dialStyle)

    },

    genCards: function() {
      const numWords = 2

      // need to pick all or more
      if (numWords >= cards.length) return cards

      let pickedCards = []
      while (pickedCards.length < numWords) {
        let pick = cards[Math.floor(Math.random() * cards.length)]

        // no dupes
        while (pickedCards.includes(pick)) {
          pick = cards[Math.floor(Math.random() * cards.length)]
        }

        pickedCards.push(pick)
      }

      window.console.log(pickedCards)
      this.cards = pickedCards

      // remove seen words?
     

    }
  },
}




</script>

<style lang="scss">

