  $slice-delta: 7deg;
  $angle-origin: 180deg - ($slice-delta/2);

  $diam: 700px;
  $dial-radius: $diam*(1/11);
  $needle-height: $diam/2.7;
  $needle-width: $diam/70;

  $color-border: #0d0e32;
  $color-card-text: #231f20;
  $color-dial: #cf2535;
  $color-view: #6db6b2;
  $color-base: #f4ede1;



  #app {
    font-family: 'Neutraface', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;

    user-select: none;
  }

  #desc {
    font-style: italic;
    color: rgb(124, 129, 137);
  }

  button {
    width: 20rem;
    height: 5rem;
    font-family: 'Neutraface', Arial;
    font-size: 2rem;
  }

  #cards {
    display: flex;
    justify-content: space-around;

    font-size: 2rem;
    padding-left: 0;

    // hyphens: auto;
  }

  .card {
    width: 30%;
    height: 10rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border: 1px black solid;
