    background-color: $color-base;
  }

  .card-left::before {
    content: "←";
  } 

  .card-right::before {
    content: "→";
  } 


  .fade-enter-active, .fade-leave-active {
    // transition: opacity 0.5s ease-in-out;
    transition: transform 1s ease-out;
  }

