    overflow: hidden;
    position: relative;
    margin: 10px;

    #dial {      
      height: inherit;
      width: inherit;
      position: absolute;
      transform-origin: 50% 100%;
      #dial-knob {        
        width: $dial-radius*2;
        height: $dial-radius*2;         
        border-radius: 50%;
