    }

    .spec-border {      
      box-sizing: border-box;
      height: inherit;
      width: inherit;
      position: absolute;      
      border-radius: $diam/2 $diam/2 0 0;
      border: $color-border solid $diam*(3/70);
      border-bottom: $color-border solid $diam*(0/70);
    }

    
    .spec-extra {      
      height: inherit;
      width: $diam;
      position: absolute;    
      border-radius: $diam/2 $diam/2 0 0;
