      position: absolute;
      transform-origin: 50% 100%;
      transition: transform 0.5s ease-in-out;
      // transform: rotate(-$angle);
    }

    .spec-slice {
      height: inherit;
      width: inherit;
      position: absolute;      
      border-radius: $diam/2 $diam/2 0 0;
