      }

      &.med {
        background-color: #ef473c;
        &.l {
          transform: rotate($angle-origin - $slice-delta);
        }
        &.r {
          transform: rotate($angle-origin + $slice-delta);
        }
      }

      &.big {
        background-color: #ebaa21;        
        &.l {
          transform: rotate($angle-origin - 2*$slice-delta);
        }
        &.r {
